'use client';

import Link from 'next/link';
import { getUserBadge } from '@/interfaces/Badge';

interface LeaderboardCardProps {
    rank: number;
    type: 'story' | 'author' | 'badge';
    data: any;
}

export default function LeaderboardCard({ rank, type, data }: LeaderboardCardProps) {
    const getRankIcon = (rank: number) => {
        switch (rank) {
            case 1: return 'ü•á';
            case 2: return 'ü•à';
            case 3: return 'ü•â';
            default: return `${rank}.`;
        }
    };

    if (type === 'story') {
        return (
            <Link href={`/story/${data.id}`} className="leaderboard-card">
                <div className="rank-badge">{getRankIcon(rank)}</div>
                <div className="card-content">
                    <h3 className="card-title">{data.title}</h3>
                    <div className="card-meta">
                        <span className="meta-item">
                            <span className="meta-icon">üë§</span>
                            {data.author.name}
                        </span>
                        <span className="meta-item">
                            <span className="meta-icon">üìÅ</span>
                            {data.category}
                        </span>
                    </div>
                </div>
                <div className="card-stats">
                    <div className="stat-item">
                        <span className="stat-icon">üëÅÔ∏è</span>
                        <span className="stat-value">{data.stats.views.toLocaleString()}</span>
                    </div>
                    <div className="stat-item">
                        <span className="stat-icon">üí¨</span>
                        <span className="stat-value">{data.stats.comments}</span>
                    </div>
                    <div className="stat-item">
                        <span className="stat-icon">‚ù§Ô∏è</span>
                        <span className="stat-value">{data.stats.likes}</span>
                    </div>
                </div>
            </Link>
        );
    }

    if (type === 'author') {
        // Use pre-calculated badge from user data instead of recalculating
        const badgeEmoji = data.currentBadge || 'üå±';
        const badge = getUserBadge(data.totalWins || 0); // Only for badge name

        return (
            <div className="leaderboard-card">
                <div className="rank-badge">{getRankIcon(rank)}</div>
                <div className="card-content">
                    <div className="author-header">
                        <span className="author-avatar">
                            {data.photoURL ? (
                                <img src={data.photoURL} alt={data.displayName} />
                            ) : (
                                'üë§'
                            )}
                        </span>
                        <div>
                            <h3 className="card-title">{data.displayName}</h3>
                            {badge && (
                                <span className="author-badge">
                                    {badgeEmoji} {badge.name}
                                </span>
                            )}
                        </div>
                    </div>
                </div>
                <div className="card-stats">
                    <div className="stat-item">
                        <span className="stat-icon">üèÜ</span>
                        <span className="stat-value">{data.totalWins || 0}</span>
                        <span className="stat-label">Galibiyet</span>
                    </div>
                </div>
            </div>
        );
    }

    if (type === 'badge') {
        // Use pre-calculated badge from user data
        const badgeEmoji = data.currentBadge || 'üå±';
        const badge = getUserBadge(data.totalWins); // Only for badge name

        return (
            <div className="leaderboard-card">
                <div className="rank-badge">{getRankIcon(rank)}</div>
                <div className="card-content">
                    <div className="author-header">
                        <span className="author-avatar">
                            {data.photoURL ? (
                                <img src={data.photoURL} alt={data.displayName} />
                            ) : (
                                'üë§'
                            )}
                        </span>
                        <div>
                            <h3 className="card-title">{data.displayName}</h3>
                            {badge && (
                                <span className="author-badge badge-highlight">
                                    {badgeEmoji} {badge.name}
                                </span>
                            )}
                        </div>
                    </div>
                </div>
                <div className="card-stats">
                    <div className="stat-item">
                        <span className="stat-icon">üèÜ</span>
                        <span className="stat-value">{data.totalWins}</span>
                        <span className="stat-label">Toplam Galibiyet</span>
                    </div>
                </div>
            </div>
        );
    }

    return null;
}
